/*
* @summary style variables
*/
body {
  background: #bfbfbf !important; }

/* General */
.sesc-section p {
  text-align: center; }

/*helpers*/
.sesc-no-margin {
  margin: 0;
  margin-right: 0;
  margin-left: 0;
  margin-bottom: 0;
  margin-top: 0; }

.sesc-no-padding {
  padding: 0;
  padding-right: 0;
  padding-left: 0;
  padding-bottom: 0;
  padding-top: 0;
  max-width: 1600px; }

.screen-reader-text {
  /* Reusable, toolbox kind of class */
  position: absolute;
  top: -9999px;
  left: -9999px; }

/* overrides */
#wrapper {
  max-width: 1220px !important; }
  #wrapper #inner-wrapper {
