/* From ARIA */
/* Extra small devices (phones, less than 768px) */
/* No media query since this is the default in Bootstrap */
/* Small devices (tablets, 768px and up) */
@media screen and (min-width: 768px) {
  #wrapper #inner-wrapper {
    border-bottom-left-radius: 3em;
    border-bottom-right-radius: 3em; }
    #wrapper #inner-wrapper header {
      border-right: 3px solid #2091cd;
      border-left: 3px solid #2091cd; }
    #wrapper #inner-wrapper #nomadHeader .headerSocialMediaLogin ul {
      float: right; }
    #wrapper #inner-wrapper #nomadHeader .headerSocialMediaLogin .searchform {
      width: 50%;
      margin: 1% auto; }
    #wrapper #inner-wrapper #main_nav_container ul li a {
      padding: .5em;
      font-size: .95em; }
    #wrapper #inner-wrapper .navbar {
      border-radius: 0;
      border: none;
      margin-bottom: 0;
      border-color: #2091cd;
      border-right: 3px solid #2091cd;
      border-left: 3px solid #2091cd; }
    #wrapper #inner-wrapper .sesc-home-main-article {
      border-right: 3px solid #2091cd;
      border-left: 3px solid #2091cd; }
    #wrapper #inner-wrapper .sesc-home-posts .featured {
      width: 50%;
      margin: 0 4% 0 0; }
    #wrapper #inner-wrapper .main-nav {
      position: relative; }
    #wrapper #inner-wrapper #header {
      padding-top: 20px; }
    #wrapper #inner-wrapper .sesc-home-slider {
      display: block;
      margin: 2em 0 0 0; }
      #wrapper #inner-wrapper .sesc-home-slider .carousel-inner {
        height: 300px; }
      #wrapper #inner-wrapper .sesc-home-slider img {
        display: block;
        display: block; }
      #wrapper #inner-wrapper .sesc-home-slider .carousel-indicators {
        left: 15%;
        bottom: 20%; }
      #wrapper #inner-wrapper .sesc-home-slider .sesc-slider-excerpt {
        position: absolute;
        top: 0;
        left: 0;
        width: 30%;
        background: rgba(0, 0, 0, 0.5);
        height: 100%;
        text-align: center;
        overflow: hidden; }
        #wrapper #inner-wrapper .sesc-home-slider .sesc-slider-excerpt h1 {
          color: #ffffff;
          font-size: 1.25em;
          margin: 10% auto;
          text-align: center;
          margin: 2% auto;
