      border-bottom: 3px solid #2091cd; }
    #wrapper #inner-wrapper .sesc-call-to-action-footer .sesc-footer-admin {
      border-bottom: 3px solid #2091cd;
      background: rgba(83, 142, 215, 0.74); }
    #wrapper #inner-wrapper .sesc-call-to-action-footer .sesc-footer-families {
      border-bottom: 3px solid #2091cd; }
    #wrapper #inner-wrapper .sesc-call-to-action-footer .sesc-footer-partners {
      border-bottom: 3px solid #2091cd;
      border-right: 3px solid #2091cd; }
  #wrapper #footer #sesc-footer-nav li {
    display: inline-block; } }

/* Medium devices (desktops, 992px and up) */
@media screen and (min-width: 992px) {
  #wrapper #inner-wrapper .sesc-home-posts {
    border: 3px solid #2091cd;
    margin: 1em 0; }
  #wrapper #inner-wrapper #nomadHeader .headerSocialMediaLogin .searchform {
    width: 100%; }
  #wrapper #inner-wrapper #main_nav_container ul li a {
    padding: .5em 1em; }
  #wrapper #inner-wrapper .sesc-home-slider img {
    height: 400px; }
  #wrapper #inner-wrapper .sesc-home-slider .carousel-indicators {
    bottom: 5%; }
  #wrapper #inner-wrapper .sesc-home-slider .carousel-inner {
    height: 400px; }
  #wrapper #inner-wrapper .sesc-home-slider .sesc-slider-excerpt h1 {
    font-size: 1.5em;
    line-height: 1.25em; }
  #wrapper #inner-wrapper .sesc-call-to-action-footer a {
    color: #ffffff;
    display: block;
    padding: 2em; }
  #wrapper #inner-wrapper .sesc-call-to-action-footer .sesc-footer-teachers {
    border-bottom-left-radius: 1.5em; }
  #wrapper #inner-wrapper .sesc-call-to-action-footer .sesc-footer-partners {
    border-bottom-right-radius: 1.5em; } }

